<template>
  <main class="prescription__template__main">
    <section class="box__shadow__01 vertical__scroll">
      <h3 class="page__title">
        {{ this.$store.state.pageInfo["처방전 템플릿"].title }}
      </h3>
      <h5 class="page__sub__title f__regular">
        {{ this.$store.state.pageInfo["처방전 템플릿"].subTitle }}
      </h5>
      <ul class="search__wrap">
        <li>
          <select name="" id="" v-model="useYn">
            <option value="">전체</option>
            <option value="Y">사용</option>
            <option value="N">미사용</option>
          </select>
        </li>
        <li>
          <input class="custom__input" type="text" v-model="templateName" />
        </li>
        <li>
          <button class="custom__btn" @click="getPrescriptionTemplate">
            <span
              ><img src="@/assets/images/icon/ic-search.svg" alt="검색 아이콘"
            /></span>
            <p>검색</p>
          </button>
        </li>
      </ul>
      <div class="grid__button__wrap">
        <!-- <button class="custom__btn delete">
          <span
            ><img src="@/assets/images/icon/ic-remove.svg" alt="삭제 아이콘"
          /></span>
          <p>선택 삭제</p>
        </button> -->
        <button class="custom__btn" @click="templateAddOrEdit">
          <span
            ><img src="@/assets/images/icon/ic-plus.svg" alt="플러스 아이콘"
          /></span>
          <p>템플릿 추가</p>
        </button>
      </div>
      <div class="grid__wrap" @click="rowClick($event)">
        <Grid
          class="on"
          ref="gridEl"
          :cols="cols"
          :pagination="true"
          :sort="true"
          :rows="templateList"
        >
        </Grid>
      </div>
    </section>
  </main>
</template>

<script>
import "@/assets/css/prescriptionTemplate.css";
import { Grid } from "gridjs-vue";
import data6 from "@/data/patientManage/data6.json";
export default {
  components: {
    Grid,
  },
  created() {
    this.getPrescriptionTemplate();
  },
  data() {
    return {
      useYn: "",
      templateName: "",
      selectedState: "",
      cols: [
        { name: "제목", id: "name" },
        { name: "설명", id: "description" },
        {
          name: "치료제 수",
          id: "prescriptionListByWeek",
          formatter: (cell) => {
            let size = 0;
            for (let i of cell) {
              size = size + i.treatmentList.length;
            }
            return size;
          },
        },
        {
          name: "상태",
          id: "useYn",
          formatter: (cell) => {
            if (cell === "N") {
              return "미사용";
            } else if (cell === "Y") {
              return "사용";
            }
          },
        },
        { name: "등록자명", id: "registerName" },
        { name: "변경일시", id: "updateDate" },
        {
          name: "수정",
          formatter: (cell, row) => {
            return this.$gridjs.h("button", {
              onClick: () => {
                this.$router.push({
                  name: "PrescriptionTemAddOrEdit",
                  params: {
                    name: row.cells[0].data,
                    description: row.cells[1].data,
                    useYn: row.cells[3].data,
                    prescriptionListByWeek: row.cells[2].data,
                    pTemplateId: row.cells[8].data,
                  },
                });
              },
            });
          },
        },
        {
          name: "삭제",
          formatter: (cell, row) => {
            return this.$gridjs.h("button", {
              onClick: () => {
                let name = row.cells[0].data;
                // let pTemplateId = row.cells[8].data;
                this.$confirm({
                  // showCancelButton: true,
                  title: "",
                  message: `${name} 템플릿을 삭제하시겠습니까?`,
                  button: {
                    no: "아니오",
                    yes: "네",
                  },
                  callback: (confirm) => {
                    if (confirm) {
                      //
                    }
                  },
                });
              },
            });
          },
        },
        {
          name: "템플릿 ID",
          id: "pTemplateId",
          hidden: true,
        },
      ],
      templateList: [],
    };
  },
  methods: {
    getPrescriptionTemplate() {
      this.templateList = data6;
    },
    templateAddOrEdit() {
      this.$router.push({
        name: "PrescriptionTemAddOrEdit",
      });
    },
  },
};
</script>
